.navbar-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 15vh;
  padding: 0% 15%;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.navbar-container .items-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  list-style: none;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.navbar-container .items-container .logo {
  font-size: 22px;
}

.navbar-container .items-container li {
  padding: 0% 5%;
  cursor: pointer;
}

.navbar-container .logo-container h1 {
  font-size: 40px;
}

* {
  font-family: 'Poppins', sans-serif;
}
